import React from 'react';

function Homesection() {
  const scrollToSection = (e) => {
    e.preventDefault();
    const targetId = e.currentTarget.getAttribute('data-target');
    const targetSection = document.getElementById(targetId);
    if (targetSection) {
      targetSection.scrollIntoView({ behavior: 'smooth' });
    }
  }; 


  return (
    <section id="Home" className="intro-section">
      <div className="container">
        <div className="row align-items-center text-white">
          <div className="col-md-6 col-6 intros text-start">
            <h1 className="display-2">
              <span className="display-2--intro">Hey! I'm <span style={{ textTransform: 'capitalize' }}>Tio</span><span style={{ textTransform: 'capitalize' }}>Assist</span>.<span style={{ textTransform: 'lowercase' }}>io</span></span>
              <span className="display-2--description lh-base">
                And I'm here to answer any questions you have.
                We use AI and Experts to get the right answers to you!
              </span>
            </h1>
            <div className="button-container">
      <button
        type="button"
        id="tryMeButton"
        className="rounded-pill btn-rounded"
        data-target="Let's-Chat" // Set the target section's ID here
        onClick={scrollToSection}
      >
        Try me now
        <span><i className="fas fa-arrow-right"></i></span>
      </button>
    </div>
          </div>
          <div className="col-md-6 col-6 intros text-end">
            <div className="image-box">
              <div className="image-container">
                <img src={`${process.env.PUBLIC_URL}/images/arts/TIO.webp`} alt="TIO illustration" className="img-fluid2" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
        <path fill="#ffffff" fillOpacity="1" d="M0,160L48,176C96,192,192,224,288,208C384,192,480,128,576,133.3C672,139,768,213,864,202.7C960,192,1056,96,1152,74.7C1248,53,1344,107,1392,133.3L1440,160L1440,320L1392,320C1344,320,1248,320,1152,320C1056,320,960,320,864,320C768,320,672,320,576,320C480,320,384,320,288,320C192,320,96,320,48,320L0,320Z"></path>
      </svg>
    </section>
  );
};

export default Homesection;
